import React, { Component } from 'react';
import './App.css';
import List from './components/List';
import Selecter from './components/Selecter';

//const allowedLists = ['groups','eninfoboxes'];
const config = {
  'eninfoboxes':{'action':'get_infoboxes','specific':'get_data'},
  'groups':{'action':'get_infoboxes1','specific':'get_data1'},
  'rusports':{'action':'get_infoboxes2','specific':'get_data2'}
};

export default class OneList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selValue: null,
      listName: null,
      currListConfig: {},
      listError: false
    };
  }

  setup() {
    const listName = this.props.match.params.list;
    const infobox = this.props.match.params.inf;
    //const filter = this.props.match.params.filter;
    //console.log(infobox);
    
    this.setState({listName, selValue: infobox});

    if (Object.keys(config).includes(listName)) {
      this.setState({listError: false, currListConfig: config[listName]});
    } else {
      this.setState({listError: true, selValue: null});
    }
  }
  
  componentDidMount() {
    this.setup();
  }
  
  componentDidUpdate(prevProps) {
    if (this.props.match.params.list !== prevProps.match.params.list || this.props.match.params.inf !== prevProps.match.params.inf) {
      console.log(this.props.match.params.list);
      this.setup();
    }
  }
  
  handleLanguage = (langValue) => {
    this.setState({selValue: langValue});
    //this.props.match.params.blah = langValue;
    
    this.props.history.push({
      pathname: '/'+this.props.match.params.list+ (langValue ? '/'+langValue : '')
    })
  }
  
  render() {
    const {selValue, currListConfig, listName, listError} = this.state;
	return (
<div className="container">
<form className="form-horizontal">
{listError ? <div>Nav šādas kategorijas <code>{listName}</code></div> : <div className="form-group">
{Object.keys(currListConfig).length !== 0 ? <div>
<label className="col-md-2 control-label">Izvēlies kategoriju</label>
<div className="col-sm-3">
<Selecter config={currListConfig} searchParam={selValue} onSelectLanguage={this.handleLanguage}/>
</div></div> : <div>Tiek ielādēts saturs</div>}
</div>
}
</form>
{selValue ? <div><List searchParam={selValue} config={currListConfig} /></div> : ""}
</div>

    );
  }
}


