import React, { Component } from 'react';
import {Typeahead} from 'react-bootstrap-typeahead';
import 'react-bootstrap-typeahead/css/Typeahead.css';
import { buildUrl } from '../utils';
import { apiEndpoint } from '../config';

export default class Selecter extends Component {
    constructor(props) {
      super(props);
      //this.handleChange = this.handleChange.bind(this);
      this.state = {
        error: null,
        isLoaded: false,
        posts: [],
        currItem: '',
        defaultVAL: null
      };
    }

    setData() {
      console.log(this.props.config.action);
      fetch(buildUrl(apiEndpoint,{'act':this.props.config.action}))
        .then(res => res.json())
        .then(
          (result) => {
            this.setState({
              isLoaded: true,
              posts: result,
              error: null
            });
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        )

    }
    
    componentDidMount() {
      this.setData();
      if (this.props.searchParam) {
        this.setState({
          defaultVAL: this.props.searchParam
        });
      }
    }
    
  componentDidUpdate(prevProps) {
    if (this.props.config.action !== prevProps.config.action || this.props.searchParam !== prevProps.searchParam) {
      this.setData();
    }
  }
  
      handleLangChange = (e) => {
          var lang = e.target.value;
          this.props.onSelectLanguage(lang);            
      }
  
    
    //handleChange = e => this.setState({ currItem: e.target.value });
    /*
    handleChange(e) {
        console.log(e.target.value);
        this.setState({ currItem: e.target.value })
    }
    */
    onChange(e) {
        //console.log(e.target.value);
        this.setState({ currItem: e.target.value })
    }
    
    render() {
      const { error, isLoaded, posts, selected, defaultVAL } = this.state;
      //console.log(posts);
      if (error) {
        return <div>Error: {error.message}</div>;
      } else if (!isLoaded) {
        return <div>Loading...</div>;
      } else {
        return (
          <Typeahead
            clearButton
            labelKey="a"
            multiple={false}
            options={posts}
            defaultInputValue={defaultVAL ? defaultVAL : ''}
            placeholder="Izvēlies..."
            onChange={(selected) => this.props.onSelectLanguage(selected[0])}
            selected={selected}
          />
        );
      }
    }
  }


