export const isSrcDocSupported = document.createElement('iframe').srcdoc !== undefined;

export function checkStatus(response) {
    if (response.status >= 200 && response.status < 300) {
        return response;
    } else {
        return response.json().then(Promise.reject.bind(Promise));
    }
}

export function parseJSON(response) {
    return response.json();
}

export function encodeParams(params) {
    let encodedParams = [];
    for (const key of Object.keys(params)) {
        if (params[key]) {
            encodedParams.push(key + '=' + encodeURIComponent(params[key]));
        }
    }
    return encodedParams.join('&');
}

//https://github.com/github/fetch/issues/256
//https://stackoverflow.com/questions/316781/how-to-build-query-string-with-javascript/34209399#34209399
export function buildUrl(url, parameters){
  var qs = "";
  for(var key in parameters) {
    var value = parameters[key];
    qs += encodeURIComponent(key) + "=" + encodeURIComponent(value) + "&";
  }
  if (qs.length > 0){
    qs = qs.substring(0, qs.length-1); //chop off last "&"
    url = url + "?" + qs;
  }
  return url;
}

export function compare(a, b) {
	if (a.iws > b.iws) return -1;
	if (a.iws < b.iws) return 1;
	
	if (a.orig.toUpperCase() > b.orig.toUpperCase()) return 1;
	if (a.orig.toUpperCase() < b.orig.toUpperCase()) return -1;
}



