import React, { Component } from 'react';
import { buildUrl } from '../utils';
import { apiEndpoint } from '../config';

const urlLink = (project, label) => {
	const urlEncoded = encodeURIComponent(label);
	const text = label.replace(/_/g, " ");

  return <a target="_blank" href={"https://"+project+".wikipedia.org/wiki/" + urlEncoded}>{text}</a>;
}

const headerText = (updated) => {
  return <span>Atjaunināts {updated}</span>;
};

const listItem = (index, item, lang) => {
  return <li key={index}>{urlLink(lang,item[0])} - {item[1]}</li>
};

export default class List extends Component {
  
  constructor(props) {
    super(props);
    //this.handleChange = this.handleChange.bind(this);
	//this.componentDidMount = this.componentDidMount.bind(this);
    this.state = {
      posts: [],
	  updated: '',
	  isLoaded: false,
	  hasError: false,
    filter: "",
    language: "en"
    };
    this.handleChange = this.handleChange.bind(this);
  }

  getData(searchParam) {
    const finalURL = buildUrl(apiEndpoint,{'act':this.props.config.specific,'val':searchParam});
    //console.log(finalURL);
    fetch(finalURL)
        .then(res => res.json())
        .then(
          (result) => {
            this.setState({
              isLoaded: true,
              posts: result['data'],
        updated: result['update'],
        language: (result['lang'] ? result['lang'] : "en")
            });
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
              hasError: true,
              isLoaded: true,
        updated: false,
        posts: []
            });
          }
        )
  }
  
  handleChange(event) {
    this.setState({filter: event.target.value});
  }

  componentDidMount() {
    this.getData(this.props.searchParam);
  }
  
	componentDidUpdate(prevProps) {
	  if (this.props.searchParam !== prevProps.searchParam) {
      //console.log('searchParam did change');
      this.getData(this.props.searchParam);
	  }
	}

  render() {
	//var obj = [...this.state.posts];
	//obj.sort(compare);
	//
	if (this.state.hasError) {
		return <div>Notika kļūda</div>;
	} else if (!this.state.isLoaded) {
      return <div>Tiek ielādēti dati...</div>;
    } else {
	return <div><form className="form-horizontal">
  <div className="form-group">
    <label className="col-md-2 control-label">Filtrēt</label>
    <div className="col-sm-3">
    <input type="text" className="form-control" value={this.state.filter} onChange={this.handleChange} />
    </div>
  </div>
  </form>
    {this.state.posts !== null ? <div>
	{headerText(this.state.updated)}<br /><br />
	<div className="divided-text">
  <ul>{this.state.posts
      .filter(obj => this.state.filter === '' || obj[0].toLowerCase().includes(this.state.filter.toLowerCase()))
      .map((item, index) => listItem(index, item, this.state.language))
      //this.state.posts.map((item, index) => <ListItem index={index} item={item}/>)
      //
      }</ul>
      </div>
    </div>
      : ""
    }
	</div>;
	}
  }
}


