import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { HashRouter as Router, Route, Switch  } from 'react-router-dom';
import OneList from './OneList';
import Women from './Women';
import Films from './Films';

const Dashboard = () => (
  <div className="container">
    <h2>MIS</h2>
    Pašlaik pieejamie saraksti:
    <ul>
      <li><a href="#/eninfoboxes" target="_blank">Pēc infokastes Vikipēdijā angliski</a></li>
      <li><a href="#/groups" target="_blank">Dažādas grupas</a></li>
      <li><a href="#/women" target="_blank">Sievietes (pēc laika perioda)</a></li>
      <li><a href="#/films" target="_blank">Filmas (pēc laika perioda)</a></li>
      <li><a href="#/rusports" target="_blank">ruwiki sportisti</a></li>
    </ul>
  </div>
)

const NotFound = ({ location }) => (
  <div>
    <h3>Did not found page for <code>{location.pathname}</code></h3>
  </div>
)

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoggedIn: false,
      userName: null
    }
  }
  
  render() {
    return (
      <Router>
        <div>
          <Switch>
            <Route exact path="/" component={Dashboard} />
            <Route exact path="/women" component={Women} />
            <Route exact path="/films" component={Films} />
            <Route path="/:list/:inf?/:filter?" component={OneList} />
            <Route component={NotFound} />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;


