import React, { Component } from 'react';
import { buildUrl } from './utils';
import { apiEndpoint } from './config';

class UrlLink extends Component {
  render() {
    const { project, label } = this.props;
	const urlEncoded = encodeURIComponent(label);
	const text = label.replace(/_/g, " ");
	
	return <a target="_blank" href={"https://"+project+".wikipedia.org/wiki/" + urlEncoded}>{text}</a>
  }
}

class HeaderText extends Component {
  render() {
    const { updated } = this.props;
	//console.log(updated);
	return <span>Atjaunināts {updated}</span>
  }
}

//let {propOne, propTwo, ...leftOver} = this.props;
class ListItem extends Component {
  render() {
    const { index, item, lang } = this.props;
	//console.log(item);
	return <li key={index}><UrlLink label={item[0]} project={lang}/> - {item[1]} <small>({item[2]})</small></li>
  }
}

class OneList extends Component {
  render() {
    const { items, title } = this.props;

    return <div><h4>{title !== ""? title : "..."}</h4>
    <div className={items.length > 20 ? "divided-text": ""}><ul>
        {items.map((item, index) =>  
      <ListItem key={index} index={index} item={item} lang={"en"}/>)}
    </ul></div>
      </div>
  }
}

const theSortOrder = ["2010s", "2000s", "1990s", "1980s", "1970s", "1960s", "1950s", "1940s", "1930s", "1920s", "1910s", "1900s", "20. gs", "1890s", "1880s", "1870s", "1860s", "1850s", "1840s", "1830s", "1820s", "1810s", "1800s", "19. gs", "18. gs", "17. gs", "16. gs", "15. gs", "14. gs", "13. gs", "12. gs", "11. gs", "10. gs", "9. gs", "8. gs", "7. gs", "6. gs", "5. gs", "4. gs", "3. gs", "2. gs", "1. gs", "1. gs pme", "2. gs pme", "3. gs pme", "4. gs pme", "5. gs pme", "6. gs pme", "7. gs pme", "8. gs pme", "9. gs pme", "11. gs pme", "12. gs pme", "13. gs pme", "14. gs pme", "15. gs pme", "16. gs pme", "18. gs pme", "19. gs pme", "20. gs pme", "21. gs pme", "23. gs pme", "24. gs pme", "25. gs pme", "26. gs pme", "27. gs pme", "28. gs pme", "29. gs pme", "30. gs pme", "34. gs pme", "37. gs pme", "40. gs pme", "41. gs pme", "", "DIDNOT. gs", "pme"];

export default class Women extends Component {
  constructor(props) {
    super(props);
    this.state = {
      posts: [],
	  updated: '',
	  isLoaded: false,
	  hasError: false
    };
  }

  setup() {
    const finalURL = buildUrl(apiEndpoint,{'act':'get_data3','val':'women'});
    //console.log(finalURL);
    fetch(finalURL)
        .then(res => res.json())
        .then(
          (result) => {
            this.setState({
              isLoaded: true,
              posts: result['data'],
        updated: result['update']
            });
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
              hasError: true,
              isLoaded: true,
        updated: false,
        posts: []
            });
          }
        )
  }
  
  componentDidMount() {
    this.setup();
  }
  
  render() {
    const {hasError, isLoaded, posts, updated} = this.state;
    let theresults;

    theresults = <div>
      {
        theSortOrder.map((item, index) => (Object.keys(posts).includes(item) ? <OneList key={index} title={item} items={posts[item]}/> : ""))
      }
    </div>
    

    if (hasError) {
      return <div className="container">Notika kļūda</div>;
    } else if (!isLoaded) {
        return <div className="container">Tiek ielādēti dati...</div>;
      } else {
    return <div className="container">
      <HeaderText updated={updated} /><br /><br />{theresults}</div>;
    }
}
}


